class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            passwd: '',
            loadings: false
        };

    }

    onChangeName(e){
        this.setState({
            name: e.target.value
        })
    }

    onChangePasswd(e){
        this.setState({
            passwd: e.target.value
        })
    }

    enterLoading() {
        this.setState({
            loadings: true
        },()=> {
            if(this.state.name==="admin" && this.state.passwd==="123456"){
                this.props.history.push('/drag');
            } else{
                alert("密码或账户名错误！")
                this.setState({
                    loadings: false
                })
            }
            }
        );

    };

    go(num){
        console.log(123)
        switch(num){
            case "1": this.props.history.push('/click'); break;
            case "2": this.props.history.push('/drag'); break;
            case "3": this.props.history.push('/watch'); break;
            case "4": this.props.history.push('/table'); break;
            default: this.props.history.push('/delete'); break;
        }
    }

    render() {
        return (
            <>
            <div style={{position: 'fixed',left: '40%',top:'30%'}}>
                <Button type="primary" style={{display: 'block'}} onClick={this.go.bind(this,'1')}>点击组</Button>
                <Button type="primary" style={{display: 'block','margin-top':'20px'}} onClick={this.go.bind(this,'2')}>拖拽组</Button>
                <Button type="primary" style={{display: 'block','margin-top':'20px'}} onClick={this.go.bind(this,'3')}>观看组</Button>
                <Button type="primary" style={{display: 'block','margin-top':'20px'}} onClick={this.go.bind(this,'4')}>记录表</Button>
            </div>
                <div style={{position: 'fixed',left:'8%',top:'50%'}}>
                    <Dog/>
                </div>
            </>
        );
    }
}

export default Home;
